import React, { useMemo } from 'react';
import { useHistory } from 'react-router-dom';
import {
  useTable,
  useGlobalFilter,
  usePagination,
  useRowSelect,
} from 'react-table';

import { GlobalFilter } from './GlobalFilter.js';
import { useSortBy } from 'react-table/dist/react-table.development';
import Button from '../UIElements/Button.js';

import './basicTable.css';

export const RowSelection = (props) => {
  // Need this in order to push user to maintenance page for respective plug if selected.
  const history = useHistory();

  // Use memo to make useTable hook happy
  const columns = useMemo(() => props.columns, [props.columns]);
  // Use the plugs array that we received from our backend as a prop to generate our table's data.
  const data = useMemo(() => props.data, [props.data]);

  // onRowClick listener that will push our user to the maintenance page corresponding with selected plug's id.
  const onRowClick = (row) => {
    history.push('/maintenance/' + row.id);
  };

  // Initialization for our actual react-table
  // Specify columns and rows. Will return a table instance and use destructuring to gather all the parts we need.
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    page,
    nextPage,
    previousPage,
    canNextPage,
    canPreviousPage,
    pageOptions,
    prepareRow,
    state,
    setGlobalFilter,
  } = useTable(
    {
      columns,
      data,
    },
    useGlobalFilter,
    useSortBy,
    usePagination,
    useRowSelect,
  );

  // State for our searching function.
  const { globalFilter, pageIndex } = state;

  return (
    <React.Fragment>
      <div className="center">
        <GlobalFilter filter={globalFilter} setFilter={setGlobalFilter} />
      </div>
      <table {...getTableProps()}>
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column) => (
                <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                  {column.render('Header')}
                  <span>
                    {column.isSorted
                      ? column.isSortedDesc
                        ? ' '
                        : ' '
                      : ''}
                  </span>
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody {...getTableBodyProps()}>
          {page.map((row) => {
            prepareRow(row);
            return (
              <tr
                {...row.getRowProps()}
                // On click execute our handler that will setModalInfo and activate our modal.
                onClick={() => onRowClick(row.original)}
              >
                {row.cells.map((cell) => {
                  return (
                    <td {...cell.getCellProps()}>{cell.render('Cell')}</td>
                  );
                })}
              </tr>
            );
          })}
        </tbody>
      </table>

      <div className="center">
        <Button onClick={() => previousPage()} disabled={!canPreviousPage}>
          Previous
        </Button>
        <span>
          Page{' '}
          <strong className="center">
            {pageIndex + 1} of {pageOptions.length}
          </strong>{' '}
        </span>
        <Button onClick={() => nextPage()} disabled={!canNextPage}>
          Next
        </Button>
      </div>
    </React.Fragment>
  );
};
