import React, { useState, useEffect } from 'react';
import Papa from 'papaparse';

import { RowSelection } from '../components/table/RowSelection.js';
import { useHttpClient } from '../hooks/http-hook.js';
import LoadingSpinner from '../components/UIElements/LoadingSpinner.js';
import ErrorModal from '../components/UIElements/ErrorModal.js';
import Button from '../components/UIElements/Button.js';

const Advanced = () => {
  // State for our file loading
  const [file, setFile] = useState();
  // All the functions we want from our useHttpClient hook.
  const { isLoading, error, sendRequest, clearError } = useHttpClient();
  // State for our csv data we want to parse.
  const [parsedCsvData, setParsedCsvData] = useState([]);
  const fileReader = new FileReader();

  // State needed for fetching our plug data
  const [plugData, setPlugData] = useState(null);

  // Call fetch in order to gather all plug data in our database
  async function fetchPlugsHandler() {
    try {
      // Calls our getAllPlugs function on the backend.
      const response = await fetch(
        `${process.env.REACT_APP_BACKEND_URL}/plugs/`,
      );
      if (!response.ok) {
        throw new Error('Something went wrong');
      }

      const data = await response.json();
      console.log('Pulled Data: ', data.plugs);

      setPlugData(data);
    } catch (error) {}
  }

  // Will cause our fetch method to run when the page is initially rendered.
  useEffect(() => {
    fetchPlugsHandler();
  }, []);

  // Handler for when we have selected a file. Will update the file state
  const handleOnChange = (e) => {
    setFile(e.target.files[0]);
  };

  // Function that uses Papa.parse in order to parse our entire CSV into a json array.
  async function parseCSV() {
    Papa.parse(file, {
      // Yes we have a header row.
      header: true,
      // It is a csv so delimited by commas.
      delimiter: ',',
      // When it completes do.... set our parsed data and also print to console for now for debugging purposes.
      complete: (results) => {
        setParsedCsvData(results.data);
        console.log('COMPLETE', results.data);
      },
      // If it runs into an error then it will print out to our console ERROR and the error message.
      error: (err) => console.log('ERROR', err),
    });
  }

  // Handle on submit of import button. This will initiate the actual importing of the file.
  const handleOnSubmitImport = (e) => {
    // Prevent our page from refreshing on submit.
    e.preventDefault();

    // If we have a file selected then use fileReader to read and then parse.
    if (file) {
      fileReader.onload = function (event) {
        // If file exists run our parseCSV which will use Papa.parse to parse our csv into json.
        parseCSV();
      };

      // Do the actual reading.
      fileReader.readAsText(file);
    }
  };

  const downloadCSVFromJson = (filename, arrayOfJson) => {
    // convert JSON to CSV
    const replacer = (key, value) => (value === null ? '' : value);
    const header = Object.keys(arrayOfJson[0]);
    let csv = arrayOfJson.map((row) =>
      header
        .map((fieldName) => JSON.stringify(row[fieldName], replacer))
        .join(','),
    );
    csv.unshift(header.join(','));
    csv = csv.join('\r\n');

    // Create link and download
    var link = document.createElement('a');
    link.setAttribute(
      'href',
      'data:text/csv;charset=utf-8,%EF%BB%BF' + encodeURIComponent(csv),
    );
    link.setAttribute('download', filename);
    link.style.visibility = 'hidden';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const handleOnSubmitExport = (e) => {
    e.preventDefault();

    // If we have received plugData back from the database.
    if (plugData) {
      // This allows us to change the header names to match what we will expect when importing again.
      // Also important to note we drop a few headers suck as the id, which can be added later if needed.
      var formattedPlugExport = plugData.plugs.map(
        ({
          brick_identifier: Brick_Identifier,
          building: Building_ID,
          building_name: Building,
          energy_conservation_permission: Energy_Conservation_Permission,
          load_brand: Load_Brand,
          load_model: Load_Model,
          load_type: Load_Type,
          mac: MAC,
          occupant_contact: Occupant_Contact,
          occupant_name: Occupant_Name,
          room: Room,
          room_type: Room_Type,
          occupant_modified_date: Occupant_Modified_Data,
        }) => ({
          MAC,
          Building,
          Building_ID,
          Room,
          Load_Type,
          Load_Brand,
          Load_Model,
          Occupant_Name,
          Occupant_Contact,
          Occupant_Modified_Data,
          Brick_Identifier,
          Energy_Conservation_Permission,
          Room_Type,
        }),
      );

      // Calls our function to do the actual downloading.
      downloadCSVFromJson(`ExportedPlugData.csv`, formattedPlugExport);
    }
  };

  // Handle our Upload to Database button. Will push the creation of each row's plug into our database if they aren't already there.
  const handleOnSubmitUpload = async (e) => {
    // Prevent our page from refreshing on submit.
    e.preventDefault();

    // For every plug in our parsed csv try to create a new plug.
    for (var i = 0; i < parsedCsvData.length; i++) {
      var obj = parsedCsvData[i];

      // First lets check to see if this plug already exists in the database (Use GET)
      // TODO: Create a function that will check if a Plug exists from it's MAC. (Currently no easy way to find a plug other then it's unique id.)

      // Do a small validation here so that we don't even attempt if their is no building id.
      if (obj.Building_ID === ' ') {
        console.log('No Building ID please add one and try again.');
      } else {
        // Let's create a new plug using all the given data and POST to database. If the MAC already exists we will get an error.
        try {
          // Create our response. sendRequest is a function we created in the useHttpHook so look there for more context if desired.
          const response = await sendRequest(
            // Start with the address
            `http://localhost:5000/api/plugs/`,
            // We are trying to create a new plug so will send a POST request
            'POST',
            // Stringify our JSON so that our backend will play nice.
            JSON.stringify({
              mac: parsedCsvData[i].MAC,
              building: parsedCsvData[i].Building_ID,
              building_name: parsedCsvData[i].Building,
              room: parsedCsvData[i].Room,
              load_type: parsedCsvData[i].Load_Type,
              load_brand: parsedCsvData[i].Load_Brand,
              load_model: parsedCsvData[i].Load_Model,
              occupant_name: parsedCsvData[i].Occupant_Name,
              occupant_contact: parsedCsvData[i].Occupant_Contact,
              occupant_modified_date: parsedCsvData[i].Occupant_Modified_Date,
              brick_identifier: parsedCsvData[i].Brick_Identifier,
              energy_conservation_permission:
                parsedCsvData[i].Energy_Conservation_Permission,
              room_type: parsedCsvData[i].Room_Type,
            }),
            // VERY VERY Important, if you don't have this the backend defaults to something weird and will break everything.
            // This tells backend that we are sending our body as json so it knows how to read it.
            { 'Content-Type': 'application/json' },
          );
          // If our response is not ok (meaning we got an error).
          if (!response.ok) {
            throw new Error('Something went wrong');
          }
        } catch (err) {}
      }

      // Print out the currently selected plug to console for debugging.
      console.log(obj);
    }
  };

  return (
    <React.Fragment>
      {/* Added error modal that will pop up if any errors occur on page. */}
      <ErrorModal error={error} onClear={clearError} />
      <div style={{ textAlign: 'center' }}>
        {/* Create a Card that will hold the import button and file name. */}
        <div className="card">
          <div className="card__header">
            <h3>Import Plug Data CSV </h3>
          </div>
          <div className="card__body">
            <form>
              <input
                type={'file'}
                id={'csvFileInput'}
                accept={'.csv'}
                onChange={handleOnChange}
              />

              <Button
                onClick={(e) => {
                  handleOnSubmitImport(e);
                }}
              >
                IMPORT CSV
              </Button>
            </form>
          </div>
        </div>

        <br />

        {/* Create a Card that will hold the import button and file name. */}
        <div className="card">
          <div className="card__header">
            <h3>Export Plug Data CSV </h3>
          </div>
          <div className="card__body">
            <form>
              <input
                type={'file'}
                id={'csvFileOuput'}
                accept={'.csv'}
                onChange={handleOnChange}
              />

              <Button
                onClick={(e) => {
                  handleOnSubmitExport(e);
                }}
              >
                EXPORT CSV
              </Button>
            </form>
          </div>
        </div>

        <br />

        {isLoading && (
          <div className="center">
            <LoadingSpinner />
          </div>
        )}
        {/* As long as we have parsedCsvData then we will populate our table */}
        {!isLoading && parsedCsvData && (
          <div className="card">
            <div className="card__header">
              <h3>Imported CSV </h3>
            </div>
            <div className="card__body">
              {/* Call our RowSelection component to create our fancy table. */}
              <RowSelection
                // Set our data to be equal to what we just parsed from the uploaded file.
                data={parsedCsvData}
                // TODO: Add a way to do this more programmatically so that any csv uploaded will populate our table.
                // Designate our columns to be the following:
                columns={[
                  { Header: 'MAC', accessor: 'MAC' },
                  { Header: 'Building', accessor: 'Building' },
                  { Header: 'Room', accessor: 'Room' },
                  { Header: 'Room Type', accessor: 'Room_Type' },
                  { Header: 'Load Type', accessor: 'Load_Type' },
                  { Header: 'Load Brand', accessor: 'Load_Brand' },
                  { Header: 'Load Model', accessor: 'Load_Model' },
                  { Header: 'Occupant Name', accessor: 'Occupant_Name' },
                  { Header: 'Occupant Contact', accessor: 'Occupant_Contact' },
                  {
                    Header: 'Occupant Last Modified',
                    accessor: 'Occupant_Modified_Data',
                  },
                  {
                    Header: 'Conservation Permission',
                    accessor: 'Energy_Conservation_Permission',
                  },
                ]}
              />
            </div>
          </div>
        )}

        <br />

        <Button
          onClick={(e) => {
            handleOnSubmitUpload(e);
          }}
        >
          Upload to Database
        </Button>
      </div>
    </React.Fragment>
  );
};

export default Advanced;
