import React from 'react';
import { Link } from 'react-router-dom';

import company_logo from '../../assets/images/UCSD_logo.png';
import project_logo from '../../assets/images/favicon.svg';
import sidebar_items from './sidebar_routes.json';

import './sidebar.css';

const SidebarItem = (props) => {
  const active = props.active ? 'active' : '';

  return (
    <div className="sidebar__item">
      <div className={`sidebar__item-inner ${active}`}>
        <i className={props.icon}></i>
        <span>{props.title}</span>
      </div>
    </div>
  );
};

const Sidebar = (props) => {
  const activeItem = sidebar_items.findIndex(
    (item) => item.route === props.location.pathname,
  );

  return (
    <div className="sidebar">
      <div className="sidebar__logo">
        <img src={project_logo} alt="project logo" />
        P.L.U.G.S
      </div>
      {sidebar_items.map((item, index) => (
        <Link to={item.route} key={index}>
          <SidebarItem
            title={item.display_name}
            icon={item.icon}
            active={index === activeItem}
          />
        </Link>
      ))}
      <div className="sidebar__logo bottom">
        <img src={company_logo} alt="company logo" />
      </div>
    </div>
  );
};

export default Sidebar;
