import { useState, useCallback, useEffect } from 'react';

let logoutTimer;

export const useAuth = () => {
  const [token, setToken] = useState(false);
  const [tokenExpirationDate, setTokenExpirationDate] = useState();
  const [userId, setUserId] = useState(false);
  const [userName, setUserName] = useState(false);

  const login = useCallback((uid, token, userName, expirationDate) => {
    setToken(token);
    setUserId(uid);
    setUserName(userName);
    // Create a new date object that is plus 1 hour.
    const tokenExpiration =
      expirationDate || new Date(new Date().getTime() + 1000 * 60 * 60);
    setTokenExpirationDate(tokenExpiration);
    // Store some user data in local storage for persistent access
    localStorage.setItem(
      'userData',
      JSON.stringify({
        userId: uid,
        token: token,
        expiration: tokenExpiration.toISOString(),
        userName: userName,
      }),
    );
  }, []);

  const logout = useCallback(() => {
    setToken(null);
    setTokenExpirationDate(null);
    setUserId(null);
    setUserName(null);
    // Remove our local storage user data in order to truly log out.
    localStorage.removeItem('userData');
  }, []);

  // If our token changes we want to update the logoutTimer
  useEffect(() => {
    if (token && tokenExpirationDate) {
      // Need to call getTime() to get both in milliseconds
      const remainingTime =
        tokenExpirationDate.getTime() - new Date().getTime();
      logoutTimer = setTimeout(logout, remainingTime);
    } else {
      clearTimeout(logoutTimer);
    }
  }, [token, logout, tokenExpirationDate]);

  // Only run this the first time the element mounts.
  useEffect(() => {
    // Convert our local stored item and extract it back to a js object
    const storedData = JSON.parse(localStorage.getItem('userData'));

    if (
      storedData &&
      storedData.token &&
      new Date(storedData.expiration) > new Date()
    ) {
      login(
        storedData.userId,
        storedData.token,
        storedData.userName,
        new Date(storedData.expiration),
      );
    }
  }, [login]);

  return { token, login, logout, userId, userName };
};
