import React, { useContext } from 'react';

import { AuthContext } from '../../context/auth-context.js';
import Button from '../../components/UIElements/Button.js';

import './topnav.css';

const TopNav = () => {
  // This will hold the most recent context for us. The context contains the following:
  // token, login (function), logout (function), userId, and userName
  // Those functions are defined within the auth-hook.js found at ../hooks/auth-hook.js
  const auth = useContext(AuthContext);

  return (
    // Class names below are important since they help keep everything to the right through our css
    <div className="topnav">
      <div className="topnav__right">
        <div className="topnav__right-item">
          {/* If our auth.userName is not undefined then add the user name */}
          {typeof auth.userName !== 'undefined' && auth.isLoggedIn && (
            <div className="topnav__right-user__name">{auth.userName}</div>
          )}
        </div>
        <div className="topnav__right-item">
          {/* If we are logged in add a logout button. If not logged in show nothing. */}
          {auth.isLoggedIn && (
            <div>
              <Button onClick={auth.logout}>LOGOUT</Button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default TopNav;
