import React, { useState, useEffect, useContext } from 'react';
import { useParams, useHistory } from 'react-router-dom';

import { useHttpClient } from '../hooks/http-hook.js';
import { useForm } from '../hooks/form-hook.js';
import { VALIDATOR_REQUIRE } from '../util/validators.js';
import { AuthContext } from '../context/auth-context.js';
import LoadingSpinner from '../components/UIElements/LoadingSpinner.js';
import Button from '../components/UIElements/Button.js';
import Input from '../components/input/Input.js';
import ErrorModal from '../components/UIElements/ErrorModal.js';

const EditPlug = () => {
  const auth = useContext(AuthContext);

  // State used to store our loaded plugs data.
  const [loadedPlug, setLoadedPlug] = useState();
  // Grab all the methods we need from our useHttpClient hook.
  const { isLoading, error, sendRequest, clearError } = useHttpClient();

  // Our plug id taken from the url which the useParams allows us to grab.
  const plugId = useParams().plugId;

  console.log(plugId);

  // Need this in order to push the user to another page.
  const history = useHistory();

  // Setting up our form state
  const [formState, inputHandler, setFormData] = useForm(
    {
      mac: {
        value: '',
        isValid: false,
      },
      building_name: {
        value: '',
        isValid: false,
      },
      room: {
        value: '',
        isValid: false,
      },
      room_type: {
        value: '',
        isValid: false,
      },
      load_type: {
        value: '',
        isValid: false,
      },
      load_brand: {
        value: '',
        isValid: false,
      },
      load_model: {
        value: '',
        isValid: false,
      },
      occupant_name: {
        value: '',
        isValid: false,
      },
      occupant_contact: {
        value: '',
        isValid: false,
      },
      occupant_modified_date: {
        value: '',
        isValid: false,
      },
      energy_conservation_permission: {
        value: '',
        isValid: false,
      },
      brick_identifier: {
        value: '',
        isValid: false,
      },
    },
    false,
  );

  // Send a request and get our data for the specific plug
  useEffect(() => {
    const fetchPlug = async () => {
      try {
        const responseData = await sendRequest(
          `${process.env.REACT_APP_BACKEND_URL}/plugs/${plugId}`,
        );

        // Useful if needing to debug.
        // console.log(responseData);

        // Set our loadedPlug data
        setLoadedPlug(responseData.plug);
        // Update our form data as well
        setFormData(
          {
            mac: {
              value: responseData.plug.mac,
              isValid: true,
            },
            building_name: {
              value: responseData.plug.building_name,
              isValid: true,
            },
            room: {
              value: responseData.plug.room,
              isValid: true,
            },
            room_type: {
              value: responseData.plug.room_type,
              isValid: true,
            },
            load_type: {
              value: responseData.plug.load_type,
              isValid: true,
            },
            load_brand: {
              value: responseData.plug.load_brand,
              isValid: true,
            },
            load_model: {
              value: responseData.plug.load_model,
              isValid: true,
            },
            occupant_name: {
              value: responseData.plug.occupant_name,
              isValid: true,
            },
            occupant_contact: {
              value: responseData.plug.occupant_contact,
              isValid: true,
            },
            occupant_modified_date: {
              value: responseData.plug.occupant_modified_date,
              isValid: true,
            },
            energy_conservation_permission: {
              value: responseData.plug.energy_conservation_permission,
              isValid: true,
            },
            brick_identifier: {
              value: responseData.plug.brick_identifier,
              isValid: true,
            },
          },
          true,
        );
      } catch (err) {
        console.log('ERROR: ', err);
      }
    };
    fetchPlug();
  }, [sendRequest, plugId, setFormData]);

  // Handler of when we try to update a plug.
  const plugSubmitHandler = async (event) => {
    event.preventDefault();
    try {
      // Send a PATCH request to update the plugs metadata
      await sendRequest(
        `${process.env.REACT_APP_BACKEND_URL}/plugs/${plugId}`,
        'PATCH',
        JSON.stringify({
          mac: formState.inputs.mac.value,
          // This is important since we aren't actually changing these two due to issues in the database if we do.
          // TODO: Add further functionality to allow changing of the building and in turn updating the building id linked to it.
          building: loadedPlug.building,
          building_name: loadedPlug.building_name,
          room: formState.inputs.room.value,
          room_type: formState.inputs.room_type.value,
          load_type: formState.inputs.load_type.value,
          load_brand: formState.inputs.load_brand.value,
          load_model: formState.inputs.load_model.value,
          occupant_name: formState.inputs.occupant_name.value,
          occupant_contact: formState.inputs.occupant_contact.value,
          // Another one that we will not actually change on the site. This will be handled by the database if the occupant has been updated.
          occupant_modified_date: loadedPlug.occupant_modified_date,
          brick_identifier: loadedPlug.brick_identifier,
          energy_conservation_permission:
            formState.inputs.energy_conservation_permission.value,
        }),
        {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + auth.token,
        },
      );
      // After hitting submit redirect the user back to the maintenance table.
      history.push('/PLUGS/maintenance/');
    } catch (err) {
      console.log('ERROR:', err);
    }
  };

  // If our page is loading show a loading spinner.
  if (isLoading) {
    return (
      <div className="center">
        <LoadingSpinner />
      </div>
    );
  }

  // If our plug can't load or we have an error then tell the user.
  if (!loadedPlug && !error) {
    return (
      <div className="center">
        <div className="card">
          <div className="card__body">
            <p>Could not find plug!</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <React.Fragment>
      <ErrorModal error={error} onClear={clearError} />
      {!isLoading && loadedPlug && (
        <div className="col-12">
          <div className="card">
            <div className="card__body">
              <form className="plug-edit-form" onSubmit={plugSubmitHandler}>
                <Input
                  id="mac"
                  element="input"
                  type="text"
                  label="MAC"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid mac."
                  onInput={inputHandler}
                  initialValue={loadedPlug.mac}
                  initialValid={true}
                />
                <Input
                  id="building_name"
                  element="text"
                  label="Building"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid building name."
                  onInput={inputHandler}
                  initialValue={loadedPlug.building_name}
                  initialValid={true}
                />
                <Input
                  id="room"
                  element="text"
                  label="Room"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid room."
                  onInput={inputHandler}
                  initialValue={loadedPlug.room}
                  initialValid={true}
                />
                <Input
                  id="room_type"
                  element="text"
                  label="Room Type"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid room type."
                  onInput={inputHandler}
                  initialValue={loadedPlug.room_type}
                  initialValid={true}
                />
                <Input
                  id="load_type"
                  element="text"
                  label="Load Type"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid load type."
                  onInput={inputHandler}
                  initialValue={loadedPlug.load_type}
                  initialValid={true}
                />
                <Input
                  id="load_brand"
                  element="text"
                  label="Load Brand"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid load brand."
                  onInput={inputHandler}
                  initialValue={loadedPlug.load_brand}
                  initialValid={true}
                />
                <Input
                  id="load_model"
                  element="text"
                  label="Load Model"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid load model."
                  onInput={inputHandler}
                  initialValue={loadedPlug.load_model}
                  initialValid={true}
                />
                <Input
                  id="occupant_name"
                  element="text"
                  label="Occupant Name"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid occupant name."
                  onInput={inputHandler}
                  initialValue={loadedPlug.occupant_name}
                  initialValid={true}
                />
                <Input
                  id="occupant_contact"
                  element="text"
                  label="Occupant Contact"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid email address."
                  onInput={inputHandler}
                  initialValue={loadedPlug.occupant_contact}
                  initialValid={true}
                />
                <Input
                  id="energy_conservation_permission"
                  element="text"
                  label="Energy Conservation Permission"
                  validators={[VALIDATOR_REQUIRE()]}
                  errorText="Please enter a valid energy conservation permission level."
                  onInput={inputHandler}
                  initialValue={loadedPlug.energy_conservation_permission}
                  initialValid={true}
                />
                <Button type="submit" disabled={!formState.isValid}>
                  UPDATE PLUG
                </Button>
              </form>
            </div>
          </div>
        </div>
      )}
    </React.Fragment>
  );
};

export default EditPlug;
