import React, { useState, useEffect } from 'react';

import CheckboxDropdown from '../components/dropdowns/CheckboxDropdown.js';
import { RowSelection } from '../components/table/RowSelection';
import LoadingSpinner from '../components/UIElements/LoadingSpinner.js';
import ErrorModal from '../components/UIElements/ErrorModal.js';
import {
  buildingFilters,
  plugLoadTypeFilters,
  timeScaleFilters,
  departmentAndFloorFilters,
} from '../assets/jsData/filters.js';

const Maintenance = () => {
  const [plugData, setPlugData] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  async function fetchPlugsHandler() {
    setIsLoading(true);
    setError(null);
    try {
      // Calls our getAllPlugs function on the backend.
      // TODO: Update to take into account the filters selected.
      const response = await fetch(
        `${process.env.REACT_APP_BACKEND_URL}/plugs/`,
      );
      if (!response.ok) {
        throw new Error('Something went wrong');
      }

      const data = await response.json();
      console.log(data);

      setPlugData(data);
    } catch (error) {
      setError(error.message);
    }
    setIsLoading(false);
  }

  // Will cause our fetch method to run when the page is initially rendered.
  useEffect(() => {
    fetchPlugsHandler();
  }, []);

  return (
    <React.Fragment>
      <ErrorModal error={error} />
      <div>
        <div className="col-12">
          <div className="row">
            <div className="col-3">
              <CheckboxDropdown
                placeholder="Building"
                options={buildingFilters}
              />
            </div>
            <div className="col-3">
              <CheckboxDropdown
                placeholder="Plug Load Type"
                options={plugLoadTypeFilters}
              />
            </div>
            <div className="col-3">
              <CheckboxDropdown
                placeholder="Time Scale"
                options={timeScaleFilters}
              />
            </div>
            <div className="col-3">
              <CheckboxDropdown
                placeholder="Department/Floor"
                options={departmentAndFloorFilters}
              />
            </div>
          </div>
        </div>
        {isLoading && (
          <div className="center">
            <LoadingSpinner />
          </div>
        )}
        {!isLoading && plugData && (
          <div className="col-12">
            <div className="card">
              <div className="card__header">
                <h3>Devices </h3>
              </div>
              <div className="card__body">
                <RowSelection
                  data={plugData.plugs}
                  columns={[
                    {
                      Header: 'MAC',
                      accessor: 'mac',
                    },
                    {
                      Header: 'Building',
                      accessor: 'building_name',
                    },
                    {
                      Header: 'Room',
                      accessor: 'room',
                    },
                    {
                      Header: 'Plug Type',
                      accessor: 'load_type',
                    },
                  ]}
                />
              </div>
            </div>
          </div>
        )}
        {!plugData && (
          <div className="col-12">
            <div className="card">
              <div className="card__header">
                <h3>Devices</h3>
              </div>
              <div className="card__body">
                <p>
                  No data found. Please make sure backend is running properly.
                </p>
              </div>
            </div>
          </div>
        )}
      </div>
    </React.Fragment>
  );
};

export default Maintenance;
