import React from 'react';
import { BrowserRouter, Redirect, Route, Switch } from 'react-router-dom';

import Sidebar from './layout/sidebar/Sidebar.js';
import TopNav from './layout/topnav/TopNav.js';

import Advanced from './pages/Advanced.js';
import Auth from './pages/Auth.js';
import Dashboard from './pages/Dashboard.js';
import EditPlug from './pages/EditPlug.js';
import Maintenance from './pages/Maintenance.js';
import Reports from './pages/Reports.js';
import ServiceTickets from './pages/ServiceTickets.js';
import { useAuth } from './hooks/auth-hook.js';

import './assets/css/App.css';
import { AuthContext } from './context/auth-context.js';

const App = () => {
  // Add our auth context that will be used throughout our App
  const { token, login, logout, userId, userName } = useAuth();

  let routes;
  // If we have a token then we can access the whole site.
  // TODO: Update this according to the permission level instead
  if (token) {
    routes = (
      <Switch>
        <Route path="/" exact component={Dashboard} />
        <Route path="/reports" component={Reports} />
        <Route path="/service_tickets" component={ServiceTickets} />
        <Route path="/maintenance/:plugId" component={EditPlug} />
        <Route path="/maintenance" component={Maintenance} />
        <Route path="/advanced" component={Advanced} />
        <Redirect to="/" />
      </Switch>
    );
  } else {
    // If not token then you are not signed in therefore the only page you see is the login page.
    routes = <Route path="/" component={Auth} />;
  }
  return (
    // The actual auth context provider. Basically it's what will keep a single copy of the context and allow the rest of the site access to it using AuthContext.
    <AuthContext.Provider
      value={{
        isLoggedIn: !!token, // '!!' will convert it to boolean so if it is truthy (string, etc) it will return true
        token: token,
        userId: userId,
        userName: userName,
        login: login,
        logout: logout,
      }}
    >
      <BrowserRouter>
        <Route
          render={(props) => (
            <div className="layout">
              <Sidebar {...props} />
              <div className="layout__content">
                <TopNav />
                <div className="layout__content-main">{routes}</div>
              </div>
            </div>
          )}
        />
      </BrowserRouter>
    </AuthContext.Provider>
  );
};

export default App;
